<template>
    <view class="xinyi-content">
        <xinyi-nav-bar title="棋子选择" leftIcon="undo-filled" right-text="退出登录"></xinyi-nav-bar>
        <uv-vtabs
            style="width: 100vw"
            :chain="false"
            :list="scenarioList"
            :barWidth="160"
            :height="height"
            @change="selectMenu"
            barBgColor="#246d25"
            :barItemStyle="{ fontSize: '20px', color: '#ffffff' }"
            :barItemActiveStyle="{ fontSize: '20px', color: '#215e21' }"
            :contentStyle="{ backgroundColor: 'transparent' }"
        >
            <uv-vtabs-item>
                <uv-image :src="src" width="70vh" height="90vh" mode="aspectFit" @click="preview"></uv-image>
                <scroll-view scroll-y class="chess-pieces-list__scroll-view">
                    <view class="chess-pieces-list__view">
                        <xinyi-data-checkbox
                            ref="xinyiDataCheck"
                            mode="tag"
                            multiple
                            v-model="checkSelect"
                            :localdata="piecesList"
                            :map="{ text: 'chessPiecesName', value: 'id' }"
                            selectedColor="#4CD964"
                        ></xinyi-data-checkbox>
                    </view>
                </scroll-view>
                <view class="chess-pieces__btn">
                    <button class="chess-pieces-all__btn" @click="selectAll()">
                        {{ allSelect ? '取消全选' : '全选' }}
                    </button>
                    <button class="chess-pieces-confirm__btn" @click="toRoom('0')">创建推演</button>
                    <button class="chess-pieces-confirm__btn" @click="toRoom('1')">立即开始</button>
                </view>
            </uv-vtabs-item>
        </uv-vtabs>
    </view>
</template>

<script>
import { getOssById } from '@/api/system.js';
import { queryVerdictScenarioVos } from '../../api/scenario.js';
import { queryVerdictChessPiecesVos } from '../../api/chessPieces.js';
import { saveVerdictScenario, saveVerdictChessPieces } from '../../api/verdictRecord.js';
import { generateInviteCode } from '@/api/room.js';
export default {
    data() {
        return {
            value: undefined,
            scenarioList: [],
            diceAmount: '',
            piecesList: [],
            productId: '',
            checkSelect: [],
            verdictRecordId: '',
            scenarioId: '',
            allSelect: false,
            user: {},
            menuIndex: 0,
            isNav: false,
            src: ''
        };
    },
    computed: {
        height() {
            return uni.getSystemInfoSync().windowHeight - 44;
        }
    },
    onLoad(option) {
        this.productId = option.id;
        this.user = uni.getStorageSync('user');
        console.log('this.user: ', this.user);
        this.queryVerdictScenarioVosFunc();
        this.queryVerdictChessPiecesVosFunc();
    },
    methods: {
        preview() {
            uni.previewImage({
                loop: true,
                urls: [this.src], //可以展示imgUrl 列表中所有的图片
                current: 0 //首先当前index对应图片，左右可以切换其他序号的图片
            });
        },
        async queryVerdictScenarioVosFunc() {
            let data = {
                id: this.productId
            };
            let res = await queryVerdictScenarioVos(data);
            this.scenarioList = res.data.data.map((item) => ({
                value: item.id,
                name: item.scenarioNumber,
                cover: item.cover
            }));
            this.scenarioId = this.scenarioList[0].value;
            if (this.scenarioList[0].cover) {
                let oss = await getOssById(this.scenarioList[0].cover);
                let coverUrl = this.baseOssIpPort + oss.data.data.fileName;
                this.src = coverUrl;
            }
        },

        async queryVerdictChessPiecesVosFunc() {
            let data = {
                id: this.productId
            };
            let res = await queryVerdictChessPiecesVos(data);
            this.piecesList = res.data.data;
            this.piecesList.forEach(async (item) => {
                if (item.chessPiecesCover) {
                    let oss = await getOssById(item.chessPiecesCover);
                    let coverUrl = this.baseOssIpPort + oss.data.data.fileName;
                    this.$set(item, 'coverUrl', coverUrl);
                }
            });
        },
        async selectMenu(index) {
            this.menuIndex = index;
            this.scenarioId = this.scenarioList[index].value;
            if (this.scenarioList[index].cover) {
                let oss = await getOssById(this.scenarioList[index].cover);
                let coverUrl = this.baseOssIpPort + oss.data.data.fileName;
                this.src = coverUrl;
            } else {
                this.src = '';
            }
        },
        selectAll() {
            this.checkSelect = [];
            this.allSelect = !this.allSelect;
            this.$refs.xinyiDataCheck.allSelectFunc(this.allSelect);
        },
        async toRoom(flag) {
            if (this.scenarioId == '') {
                uni.showToast({
                    title: '请选择想定！',
                    icon: 'none'
                });
                return;
            }
            let chessPiecesIds = '';
            this.checkSelect &&
            this.checkSelect.map((item, index) => (chessPiecesIds += index == 0 ? item : ',' + item));
            console.log('chessPiecesIds: ', chessPiecesIds);
            if (chessPiecesIds == '') {
                uni.showToast({
                    title: '请选择棋子！',
                    icon: 'none'
                });
                return;
            }
            uni.showLoading({
                title: '加载中...',
                mask: true
            });
            let scenarioData = {
                scenarioId: this.scenarioId,
                userId: this.user.id
            };
            let rst = await saveVerdictScenario(scenarioData);
            // console.log('rst: ', rst);
            this.verdictRecordId = rst.data.data.id;
            // console.log('verdictRecordId', this.verdictRecordId);
            let data = {
                chessPiecesIds: chessPiecesIds,
                verdictRecordId: this.verdictRecordId
            };
            await saveVerdictChessPieces(data);
            let res = await generateInviteCode(this.verdictRecordId);
            let inviteCode = res.data.msg;
            uni.hideLoading();
            if(flag == '1'){
                uni.navigateTo({
                    url:
                        '/pages/room/create?inviteCode=' +
                        inviteCode +
                        '&verdictRecordId=' +
                        this.verdictRecordId +
                        '&isAdmin=' +
                        true
                });
            }else{
                uni.navigateTo({
                    url: '/pages/verdict/list?productId=' + this.productId + '&isAdmin=true&isUser=false'
                });
            }

        }
    }
};
</script>

<style lang="scss">
.uv-vtabs-item {
    display: flex;
    flex-direction: row;
}
.scenario__view {
    width: 720rpx;
    display: flex;
    margin-top: 10rpx;
    align-items: center;
    .scenario-select__view {
        width: 200rpx;
    }
}
.chess-pieces-list__scroll-view {
    width: 100%;
    padding-left: 10rpx;
    height: 90vh;
}
.chess-pieces-list__view {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10rpx;
}
.chess-pieces__btn {
    position: fixed;
    right: 20rpx;
    bottom: 20rpx;
    display: flex;
    .chess-pieces-all__btn,
    .chess-pieces-confirm__btn {
        width: 100rpx;
        margin: 0;
        color: $uni-text-color-inverse;
    }
    .chess-pieces-all__btn {
        background-color: $uni-color-success;
    }
    .chess-pieces-confirm__btn {
        background-color: $uni-color-primary;
        margin-left: 10rpx;
    }
}
</style>
